

body {
    background: rgb(255, 255, 255)
}

.app__list {
    padding: 1em;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
}

.item {
    margin-right: 1em;
    margin-bottom: 1em;
    padding: 1em;
    border: 1px solid #dbdbdb;
    background-color: #f5f5f5;
}

.content {
font-size: 1.1em;
color: #1f1f1f;
/* color: rgb(14, 168, 39); */
}

.btns {
    margin-top: 1em;
}

.btn {
    margin-right: 1em;  
    padding: .4em .5em;
    cursor: pointer;
    border: 1px solid #b6b6b6;
    background-color: #f7f7f7;
    transition: .5s all ;
}

.btn:hover {
    background: #e2e2e2;
}

.input {
    margin-left: 2em;
    margin-right: 1em;
    padding: .4em .5em;
    border: 1px solid #b6b6b6;
    background-color: #f7f7f7;
    outline: 0;
}

.filter {
    margin-left: 1em;
    border: 1px dotted red;
    padding: .5em 1em;
    background: red;
    color: white;
    cursor: pointer;
    font-size: 1.1em;
}